const _hoisted_53 = /*#__PURE__*/_createStaticVNode("<div class=\"message-area\" data-v-5f3524d3><hr data-v-5f3524d3><fieldset data-v-5f3524d3><ol data-v-5f3524d3><li data-v-5f3524d3>元素チャージ効率が変化するタイプの天賦や命ノ星座は考慮しない。 </li><li data-v-5f3524d3>天賦および命ノ星座によって獲得できる元素エネルギーを計算する際に、キャラクターの天賦レベルやステータスを参照することがある。 この時のキャラクターのステータス、天賦レベル、武器、聖遺物はげんかるくのセーブデータを参照する。 セーブデータが存在しない場合は、天賦レベルを8、武器と聖遺物はなしとして計算する。<br data-v-5f3524d3></li></ol> 元素粒子の獲得ルール <ol data-v-5f3524d3><li data-v-5f3524d3>大部分のキャラクター<br data-v-5f3524d3> 元素スキル使用時に元素粒子が発生するタイプの元素スキルは、 直後のアクション実行者が元素粒子を獲得する。 </li><li data-v-5f3524d3>胡桃、宵宮、神里綾人、アルハイゼン、放浪者 等<br data-v-5f3524d3> 元素スキル使用時に元素粒子が発生せず、 その後の通常攻撃または重撃命中時に元素粒子が発生するタイプの元素スキルは、 継続時間一杯攻撃した場合に発生するであろう数の元素粒子を使用者自身が獲得する。 </li><li data-v-5f3524d3>フィッシュル、鍾離、アルベド、珊瑚宮心海、八重神子 等<br data-v-5f3524d3> 存在時間の長い設置物からの攻撃命中時に元素粒子が発生するタイプの元素スキルは、 ローテーションまたは継続時間の間ずっと攻撃し続けた場合に発生するであろう数の元素粒子を出場時間を基準に全員に按分する。 </li><li data-v-5f3524d3>アンバー、香菱、モナ、甘雨 等<br data-v-5f3524d3> 存在時間の短い設置物からの攻撃命中または設置物が破壊された時に元素粒子が発生するタイプの元素スキルについても 同様に出場時間を基準に全員に按分するが、存在時間の短さから実戦との乖離が大きくなる。（妥協） </li><li data-v-5f3524d3>雷元素共鳴が生成する元素粒子<br data-v-5f3524d3> ローテーションの間クールタイムの5秒間隔で雷元素関連反応が起き続けた場合に発生するであろう数の元素粒子を出場時間を基準に全員に按分する。 </li><li data-v-5f3524d3>西風武器が生成する元素粒子<br data-v-5f3524d3> 使用者の出場回数および出場時間(%)、ローテーションの長さから武器効果をトリガーする回数を決定する。 トリガー回数はローテーションの長さを武器効果のクールタイムで割った数を超えない。<br data-v-5f3524d3> 元素粒子は基本的に使用者が獲得するが、使用者が1アクションだけで退場する場合は次のアクション実行者が獲得する。 </li></ol></fieldset></div>", 1)

